@import '../../styles/base/index';

.assets {
  flex: 1;
  overflow: hidden;

  &--placeholder {
    display: flex;
    overflow-y: hidden;
    margin-top: rem(-64px);
  }
}

.assets__section {
  margin-bottom: $gutter;
}

.assets__header {
  display: flex;
  align-items: center;
  margin: 0 0 $gutter;
  padding: $gutter-half $gutter;
  border-radius: $border-radius;
  background-color: hsl(232, 30%, 97%);
  color: hsl(232, 16%, 58%);
}

.assets__title {
  display: flex;
  align-items: center;
  font-size: rem(13px);
  letter-spacing: rem(1.2px);
  text-transform: uppercase;
  font-weight: $semi-bold;
}

.assets__header-icon {
  margin-right: rem(10px);
  opacity: 0.9;
}

.assets__count {
  margin-left: auto;
  font-size: rem(14px);
  white-space: nowrap;
}

.assets__list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax($asset-size, 1fr));
  grid-template-rows: repeat(auto-fill, $asset-size);
}
