@import '../../styles/base/index';

$tab-bar-gutter: rem(6px);
$tab-bar-gutter-sm: rem(10px);

.tab-bar {
  display: flex;
  flex-direction: row;
  padding: $tab-bar-gutter;
  align-items: center;
  background-color: hsl(232, 39%, 51%);

  @media screen and (min-width: $breakpoint-sm) {
    flex-direction: column;
    padding: $tab-bar-gutter-sm;
  }
}

.tab-bar__logo {
  margin: 0 $gutter 0 rem(10px);
  transition: opacity 250ms ease-out;
  will-change: opacity;

  &:hover {
    opacity: 0.75;
  }

  svg {
    width: rem(20px);
  }

  @media screen and (min-width: $breakpoint-sm) {
    margin: rem(10px) 0 rem(24px);

    svg {
      width: rem(24px);
    }
  }
}

.tab-bar__section {
  display: flex;
  flex-direction: row;

  &:last-child {
    margin-left: auto;
  }

  @media screen and (min-width: $breakpoint-sm) {
    display: flex;
    flex-direction: column;

    &:last-child {
      margin-top: auto;
    }
  }
}

.tab-bar__item {
  &:not(:last-child) {
    margin-right: $tab-bar-gutter;
  }

  @media screen and (min-width: $breakpoint-sm) {
    &:not(:last-child) {
      margin-right: 0;
      margin-bottom: $tab-bar-gutter-sm;
    }
  }
}

.tab-bar__tab {
  display: flex;
  align-items: center;
  justify-content: center;
  height: rem(36px);
  width: rem(36px);
  border-radius: $border-radius;
  color: hsl(232, 100%, 92%);
  transition: background-color 200ms ease-out, color 200ms ease-out,
    transform 200ms ease-out;

  &:hover {
    background-color: hsla(0, 0%, 100%, 0.1);
    color: hsl(232, 100%, 97%);
  }

  &:active {
    transform: scale(0.95);
    color: hsl(232, 100%, 98%);
  }

  &.is-active {
    background-color: hsla(0, 0%, 100%, 0.15);
    color: hsl(232, 100%, 98%);
  }

  @media screen and (min-width: $breakpoint-sm) {
    height: rem(41px);
    width: rem(41px);
  }
}
