@import '../../styles/base/index';

.search-bar {
  position: relative;
  height: rem(38px);
  width: 100%;
  max-width: 280px;
  border-radius: $border-radius;
}

.search-bar__label {
  position: absolute;
  top: 51%;
  left: rem(14px);
  transform: translateY(-50%);
  color: hsl(232, 16%, 59%);
  z-index: 150;
  cursor: text;
}

.search-bar__input {
  width: 100%;
  padding: 0 rem(40px);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  color: hsl(232, 16%, 50%);
  background-color: hsl(232, 20%, 96%);
  border: 2px solid transparent;
  border-radius: $border-radius;
  transition: all 200ms ease-out;

  &:hover {
    background-color: hsl(232, 20%, 94%);
    color: hsl(232, 20%, 48%);
  }

  &:focus {
    border-color: hsl(232, 75%, 68%);
    background-color: $white;
    color: hsl(232, 24%, 48%);
  }

  &::placeholder {
    color: hsl(232, 16%, 50%);
  }

  &:focus::placeholder {
    color: hsl(232, 15%, 80%);
  }
}

.search-bar__reset {
  position: absolute;
  top: 50%;
  right: rem(8px);
  transform: translateY(-50%);
  color: hsl(232, 26%, 60%);
  opacity: 0.4;
  z-index: 150;
  transition: all 200ms ease-out;

  &:hover {
    opacity: 1;
  }

  &:active {
    color: hsl(232, 26%, 40%);
  }

  svg {
    max-width: rem(20px);
  }
}
