/* Minimal reset */

html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

* {
  user-select: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
}

input[type],
[contenteditable] {
  user-select: text;
}

body,
p,
h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4 {
  font-size: inherit;
  font-weight: inherit;
}

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

head,
meta,
script,
style,
title {
  display: none;
}

b {
  font-weight: inherit;
}

// Remove the focus ring on select items for Firefox.
select:-moz-focusring {
  color: transparent;
  text-shadow: none;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  display: inline-block;
  margin: 0;
  padding: 0;
  border: 0;
  background-color: transparent;
  text-align: inherit;
  font-family: inherit;
  font-size: inherit;
  white-space: nowrap;
  appearance: none;
  color: inherit;

  &:active {
    color: inherit;
  }
}

input,
fieldset {
  appearance: none;
  border: 0;
  padding: 0;
  margin: 0;
  min-width: 0;
  font-size: 1rem;
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

// Prevent number input spinner for Firefox.
input[type='number'] {
  -moz-appearance: textfield;
}

// Prevent number input spinner for WebKit.
input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  appearance: none;
}

svg {
  display: block;
}

img {
  max-width: 100%;
  display: block;
}
