@import '../../styles/base/index';

.placeholder {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  color: hsl(232, 13%, 60%);
}

.placeholder__icon {
  margin-bottom: rem(16px);
}

.placeholder__title {
  margin-bottom: rem(6px);
  font-size: rem(16px);
  font-weight: $semi-bold;
  text-transform: capitalize;
  color: hsl(232, 28%, 60%);
}

.placeholder__text {
  max-width: rem(500px);
  overflow: hidden;
  font-size: rem(16px);
  line-height: 2;
  text-align: center;
  text-overflow: ellipsis;
  color: hsl(232, 22%, 62%);

  b {
    display: inline-block;
    margin: 0 rem(2px);
    padding: 0 rem(6px);
    line-height: 1.3;
    border-radius: $border-radius;
    border: 1px solid hsl(232, 18%, 80%);
    white-space: nowrap;
  }
}
