@import '../../styles/base/index';

.sidebar {
  width: rem(238px);
  padding: $gutter;
  padding-right: $gutter - $scroll-size;
  overflow-y: scroll;
  background-color: hsl(232, 17%, 96%);

  &::-webkit-scrollbar-thumb {
    background-color: transparent;
  }

  &:hover::-webkit-scrollbar-thumb {
    background-color: $scroll-thumb-color;
  }
}

.sidebar__section {
  &:not(:first-child) {
    margin-top: rem(12px);
  }
}

.sidebar__title {
  padding: rem(12px);
  font-size: rem(13px);
  letter-spacing: rem(1.2px);
  text-transform: uppercase;
  font-weight: $semi-bold;
  color: hsl(232, 20%, 44%);
}

.sidebar__item {
  display: flex;
  align-items: center;
  width: 100%;
  min-height: rem(40px);
  padding: 0 rem(12px);
  border-radius: $border-radius;
  color: hsl(232, 20%, 45%);
  transition: background-color 200ms ease-out;

  &:hover {
    background-color: hsl(232, 27%, 93%);
  }

  &.is-active {
    background-color: hsl(232, 27%, 92%);
    color: hsl(232, 35%, 48%);
  }

  &:active {
    background-color: hsl(232, 27%, 90%);
    color: hsl(232, 24%, 35%);
  }
}

.sidebar__icon {
  flex-shrink: 0;
  margin-right: rem(10px);
  opacity: 0.9;
}

.sidebar__text {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
