body {
  min-height: $layout-min-height;
  min-width: $layout-min-width;
  font-family: $font-family;
  font-size: $font-size;
  font-weight: $font-weight;
  line-height: $line-height;
  color: $text-color;
  background-color: $background-color;
  overflow: hidden;
}

button {
  cursor: pointer;
}

svg {
  fill: currentColor;
}
