/* z-index */
.dropdown {
  z-index: 1040; }

.dropdown__content {
  position: relative;
  background-color: #fff;
  border-radius: 0.25rem;
  box-shadow: 0 0 1px 1px rgba(6, 8, 19, 0.08), 0 0.125rem 0.75rem rgba(6, 8, 19, 0.12);
  will-change: transform, opacity; }
  [data-placement*='top'] > .dropdown__content {
    margin-bottom: 0.625rem; }
  [data-placement*='right'] > .dropdown__content {
    margin-left: 0.625rem; }
  [data-placement*='bottom'] > .dropdown__content {
    margin-top: 0.625rem; }
  [data-placement*='left'] > .dropdown__content {
    margin-right: 0.625rem; }

.dropdown__arrow {
  position: absolute;
  height: 0.875rem;
  width: 0.875rem;
  transform: rotate(45deg);
  border-radius: 0.125rem;
  box-shadow: 0 0 1px 1px rgba(6, 8, 19, 0.08), 0 0 0.75rem rgba(6, 8, 19, 0.12);
  background-color: #fff;
  z-index: -1; }
  [data-placement*='top'] .dropdown__arrow {
    bottom: 0;
    margin-bottom: -0.375rem; }
  [data-placement*='right'] .dropdown__arrow {
    left: 0;
    margin-left: -0.375rem; }
  [data-placement*='bottom'] .dropdown__arrow {
    top: 0;
    margin-top: -0.375rem; }
  [data-placement*='left'] .dropdown__arrow {
    right: 0;
    margin-right: -0.375rem; }

.dropdown__list {
  padding: 0.5rem;
  border-radius: 0.25rem;
  background-color: #fff; }

.dropdown__item {
  width: 100%;
  display: flex;
  align-items: center;
  height: 2.125rem;
  padding: 0 0.875rem 0 0.25rem;
  border-radius: 0.25rem;
  color: #6b7194;
  cursor: pointer; }
  .dropdown__item:hover {
    background: #eceef9;
    color: #5b67b9; }
  .dropdown__item:active {
    background-color: #dee0ed;
    color: #5b67b9; }
  .dropdown__item:before {
    content: '';
    width: 0.875rem;
    margin-right: 0.25rem;
    font-size: 1.25rem;
    text-align: center;
    color: #2abb86; }
  .dropdown__item.is-active:before {
    content: '●'; }
