@import '../../styles/base/index';

.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  animation: app-fade-in 200ms ease-out;

  &--placeholder {
    animation: app-fade-in 1000ms ease-out;
    background-color: hsl(232, 100%, 97%);
  }

  @media screen and (min-width: $breakpoint-sm) {
    &:not(.app--placeholder) {
      flex-direction: row;
    }
  }
}

@keyframes app-fade-in {
  0% {
    opacity: 0;
  }
}
