@import '../../../styles/base/index';

.asset {
  display: flex;
  align-items: center;
  justify-content: center;
  height: $asset-size;
  width: $asset-size;
  overflow: hidden;
  border: 3px solid transparent;
  border-radius: $border-radius;
  color: hsl(232, 20%, 26%);

  &:hover {
    background-color: hsl(232, 32%, 96%);
  }

  &:active {
    color: $black;
  }

  &.is-active {
    border-color: hsl(232, 52%, 60%);
    background-color: hsl(232, 32%, 96%);
  }

  svg {
    height: rem(32px);
    max-height: 50%;
    min-height: 50%;
    min-width: 50%;
  }
}
