.btn {
  display: flex;
  align-items: center;
  height: rem(38px);
  padding: 0 rem(14px);
  border: 1px solid transparent;
  border-radius: $border-radius;
  background-color: hsl(232, 24%, 97%);
  color: hsl(232, 16%, 47%);
  font-size: 1rem;
  transition: background-color 200ms ease-out, border-color 200ms ease-out;

  &:hover {
    background-color: hsl(232, 20%, 94%);
    color: hsl(232, 20%, 36%);
  }

  &:active {
    background-color: hsl(232, 30%, 92%);
    color: hsl(232, 24%, 35%);
  }

  &--small {
    height: rem(30px);
    font-size: rem(14px);
  }

  &--full {
    width: 100%;
    justify-content: center;
  }

  &--icon-only {
    padding-left: rem(8px);
    padding-right: rem(8px);
  }

  svg {
    opacity: 0.85;
  }
}

.btn--primary {
  background-color: hsl(158, 63%, 45%);
  color: $white;

  &:hover {
    background-color: hsl(158, 60%, 43%);
    color: $white;
  }

  &:active {
    background-color: hsl(158, 60%, 40%);
    color: $white;
  }
}

.btn--outline {
  background-color: transparent;
  border-color: hsl(232, 23%, 90%);
}
