/* z-index */
.tooltip {
  z-index: 1050; }

.tooltip__content {
  position: relative;
  padding: 0.5rem 0.625rem;
  background-color: #2d3358;
  border-radius: 0.25rem;
  color: #fff;
  will-change: transform, opacity; }
  [data-placement*='top'] > .tooltip__content {
    margin-bottom: 0.625rem; }
  [data-placement*='right'] > .tooltip__content {
    margin-left: 0.625rem; }
  [data-placement*='bottom'] > .tooltip__content {
    margin-top: 0.625rem; }
  [data-placement*='left'] > .tooltip__content {
    margin-right: 0.625rem; }

.tooltip__arrow {
  position: absolute;
  height: 0.625rem;
  width: 0.625rem;
  transform: rotate(45deg);
  border-radius: 0.0625rem;
  background-color: #2d3358;
  z-index: -1; }
  [data-placement*='top'] .tooltip__arrow {
    bottom: 0;
    margin-bottom: -0.25rem; }
  [data-placement*='right'] .tooltip__arrow {
    left: 0;
    margin-left: -0.25rem; }
  [data-placement*='bottom'] .tooltip__arrow {
    top: 0;
    margin-top: -0.25rem; }
  [data-placement*='left'] .tooltip__arrow {
    right: 0;
    margin-right: -0.25rem; }
