@import '../../styles/base/index';

.shortcuts-bar {
  flex: 1;
  background-color: hsl(232, 100%, 98%);
  padding: $gutter * 3;
}

.shortcuts-bar__close {
  position: absolute;
  top: 0;
  right: 0;
  padding: $gutter * 1.6;
  opacity: 0.4;

  &:hover,
  &:focus {
    opacity: 1;
  }
}

.shortcuts-bar__title {
  margin-bottom: $gutter * 3;
  font-size: rem(24px);
  text-align: center;
  color: hsl(232, 20%, 24%);
}

.shortcuts-bar__list {
  max-width: rem(600px);
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: $gutter $gutter * 2;
}

.shortcuts-bar__item {
  display: flex;
  align-items: center;
}

.shortcuts-bar__keys {
  width: rem(80px);
  margin-right: $gutter;
  text-align: right;
}

.shortcuts-bar__key {
  display: inline-block;
  min-width: rem(40px);
  padding: rem(10px) rem(14px);
  border-radius: $border-radius;
  background-color: hsl(232, 16%, 24%);
  background-image: -webkit-gradient(
    linear,
    0% 0%,
    0% 100%,
    from(hsl(232, 16%, 32%)),
    to(hsl(232, 16%, 24%))
  );
  font-family: $font-family;
  font-weight: $book;
  color: $white;
  text-align: center;
  text-transform: capitalize;
}
