@import '../../styles/base/index';

$dropdown-color: $white;
$dropdown-offset: rem(10px);
$dropdown-translate-offset: rem(8px);
$dropdown-arrow-size: rem(14px);
$dropdown-arrow-offset: -$dropdown-arrow-size / 2 + rem(1px);

$dropdown-content-shadow: 0 0 1px 1px hsla(232, 50%, 5%, 0.08),
  0 rem(2px) rem(12px) hsla(232, 50%, 5%, 0.12);
$dropdown-arrow-shadow: 0 0 1px 1px hsla(232, 50%, 5%, 0.08),
  0 0 rem(12px) hsla(232, 50%, 5%, 0.12);

.dropdown {
  z-index: $z-index-dropdown;
}

.dropdown__content {
  position: relative;
  background-color: $dropdown-color;
  border-radius: $border-radius;
  box-shadow: $dropdown-content-shadow;
  will-change: transform, opacity;

  [data-placement*='top'] > & {
    margin-bottom: $dropdown-offset;
  }

  [data-placement*='right'] > & {
    margin-left: $dropdown-offset;
  }

  [data-placement*='bottom'] > & {
    margin-top: $dropdown-offset;
  }

  [data-placement*='left'] > & {
    margin-right: $dropdown-offset;
  }
}

.dropdown__arrow {
  position: absolute;
  height: $dropdown-arrow-size;
  width: $dropdown-arrow-size;
  transform: rotate(45deg);
  border-radius: rem(2px);
  box-shadow: $dropdown-arrow-shadow;
  background-color: $dropdown-color;
  z-index: -1;

  [data-placement*='top'] & {
    bottom: 0;
    margin-bottom: $dropdown-arrow-offset;
  }

  [data-placement*='right'] & {
    left: 0;
    margin-left: $dropdown-arrow-offset;
  }

  [data-placement*='bottom'] & {
    top: 0;
    margin-top: $dropdown-arrow-offset;
  }

  [data-placement*='left'] & {
    right: 0;
    margin-right: $dropdown-arrow-offset;
  }
}

.dropdown__list {
  padding: $gutter-half;
  border-radius: $border-radius;
  background-color: $white;
}

.dropdown__item {
  width: 100%;
  display: flex;
  align-items: center;
  height: rem(34px);
  padding: 0 rem(14px) 0 rem(4px);
  border-radius: $border-radius;
  color: hsl(232, 16%, 50%);
  cursor: pointer;

  &:hover {
    background: hsl(232, 50%, 95%);
    color: hsl(232, 40%, 54%);
  }

  &:active {
    background-color: hsl(232, 30%, 90%);
    color: hsl(232, 40%, 54%);
  }

  &:before {
    content: '';
    width: rem(14px);
    margin-right: rem(4px);
    font-size: rem(20px);
    text-align: center;
    color: hsl(158, 63%, 45%);
  }

  &.is-active:before {
    content: '●';
  }
}
