@import '../../styles/base/index';

.pane {
  display: flex;
  flex-direction: column;
  width: rem(300px);
  background-color: $white;
  border-left: 1px solid hsl(232, 10%, 90%);
  overflow-y: auto;

  .drawer > & {
    border: 0;
  }

  @media screen and (min-width: $breakpoint-xl) {
    width: rem(320px);
  }
}

.pane__file {
  display: flex;
  align-items: center;
  color: hsl(232, 20%, 52%);
}

.pane__file-icon {
  flex-shrink: 0;
  margin-right: rem(6px);
  opacity: 0.9;
}

.pane__file-name {
  margin-right: $gutter;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.pane__header {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: rem(50px);
  border-top: 1px solid hsl(232, 38%, 94%);
  border-bottom: 1px solid hsl(232, 38%, 94%);
  background-color: hsl(232, 36%, 98%);
  padding: rem(11px) $gutter;
}

.pane__title {
  font-size: rem(13px);
  letter-spacing: rem(1.2px);
  text-transform: uppercase;
  font-weight: $semi-bold;
  color: hsl(232, 16%, 51%);
}

.pane__content {
  padding: rem(20px) $gutter;

  .pane__section--footer & {
    padding: $gutter;
  }
}

.pane__preview {
  min-height: rem(122px);
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='12' viewBox='0 0 12 12' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 0h6v6H0V0zm6 6h6v6H6V6z' fill='%23F3F4F8' fill-rule='evenodd'/%3E%3C/svg%3E");
  border-radius: $border-radius;
  border: 1px solid hsl(232, 23%, 94%);
  overflow: hidden;
  color: hsl(232, 21%, 28%);

  svg {
    width: rem(36px);
    height: rem(36px);
  }
}

.pane__section {
  &--footer {
    margin-top: auto;
    border-top: 1px solid hsl(232, 38%, 94%);
  }
}

.pane__info-item {
  display: flex;
  margin-bottom: $gutter;
  justify-content: space-between;
}

.pane__info-title {
  margin-right: rem(6px);
  flex-grow: 1;
  flex-shrink: 0;
  color: hsl(232, 16%, 60%);
  text-transform: capitalize;
}

.pane__info-text {
  max-width: rem(130px);
  text-align: right;
  color: hsl(232, 16%, 50%);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.pane__range {
  position: absolute !important;
  top: 0;
  right: $gutter;
  max-width: 100px;
  height: 100%;
}
