/* Components */

$asset-size: rem(64px);
$scroll-size: rem(6px);
$scroll-thumb-color: hsl(232, 20%, 86%);

/* Layout */

$gutter: rem(16px);
$gutter-half: $gutter / 2;
$layout-min-width: rem(400px);
$layout-min-height: 100vh;

$breakpoint-sm: rem(700px);
$breakpoint-md: rem(960px);
$breakpoint-lg: rem(1200px);
$breakpoint-xl: rem(1300px);

/* Colors */

$white: #fff;
$black: hsl(232, 20%, 10%);
$dark-gray: hsl(232, 20%, 24%);

/* Font weights */

$book: 400;
$semi-bold: 500;
$bold: 700;

/* Base styles */

$font-family: 'Ubuntu', 'Helvetica Neue', Arial, sans-serif;
$font-size: 1rem;
$font-weight: $book;
$line-height: 1.2;
$text-color: hsl(233, 16%, 51%);
$background-color: $white;
$border-radius: rem(4px);

/* z-index */

$z-index-modal-overlay: 1010;
$z-index-modal: 1020;
$z-index-dropdown: 1040;
$z-index-tooltip: 1050;
